import { Map, InfoWindow, Marker, GoogleApiWrapper } from "google-maps-react";
import React from "react";

export class MapContainer extends React.Component {
  render() {
    return (
      <Map
        google={this.props.google}
        style={{width: 'auto', height: '50%', position: 'relative', margin: 'auto', maxWidth: '1000px'}}
        zoom={14}
        initialCenter={{
          lat: 22.212755,lng:-159.408257
      }}>
        <Marker 
         title={'The marker`s title will appear as a tooltip.'}
         name={'SOMA'}
         position={{lat: 22.212755,lng:-159.408257}} onClick={this.onMarkerClick}  />

        <InfoWindow onClose={this.onInfoWindowClose}>
          <div>
            <h1>{'SOMA'}</h1>
          </div>
        </InfoWindow>
      </Map>
    );
  }
}

export default GoogleApiWrapper({
  apiKey: "AIzaSyDg3lSvHa20tXyQH68ezhQE8KDjAxrdUjU"
})(MapContainer);
